* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}
.calc {
  width: 400px;
  background-color: #000000;
  color: #ffffff;
}
.screen {
  font-size: 40px;
  font-family: "Courier New", Courier, monospace;
  text-align: right;
  padding: 20px 5px;
}
.calc-button {
  background-color: #d8d9db;
  color: #000000;
  height: 100px;
  width: 24.5%;
  border: none;
  border-radius: 0;
  font-size: 40px;
  cursor: pointer;

  &:hover {
    background-color: #ebebeb;
  }

  &:active {
    background-color: #bbbcbe;
  }
}
